.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    background-color: var(--slack-color);
    color: white;
}
.header__left {
    display: flex;
    flex: 0.3;
    align-items: center;
    margin-left: 20px;

}

.header__left > .MuiSvgIcon-root {
    margin-left: auto;
    margin-right: 30px;
}
.header__Search {
    flex: 0.4;
    background-color: #421f44;
    text-align: center;
    display: flex;
    padding: 0 50px;
    border: 1px gray solid;
    border-radius: 5px;
}
.header__Search > input {
    background-color: transparent;
    border: none;
    min-width: 35vw;
    text-align: center;
}

.header__right{
    flex: 0.3;
    display: flex;
    align-items: flex-end;
}

.header__right .MuiSvgIcon-root {
    margin-left: auto;
    margin-right: 20px;
}